:root {
    --primary: black;
    --secondary: white;
    --accent: #0246cf;
    --tertiary: #cf9c02;
    --tranparent-10: #ffffff10;
    --tranparent-20: #ffffff20;
    --tranparent-30: #ffffff30;
    --tranparent-50: #ffffff50;
    --transparent-cc: #ffffffcc;

    --invert-value: 0;
}

.try {
    color: #d2d2d2
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box
}

@import url('./styles//asthetics.css');
@import url('./styles//default.css');
@import url('https://fonts.googleapis.com/css2?family=Inter+Tight:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Madimi+One&display=swap');

body {
    background: var(--primary);
    color: var(--secondary);
    font-family: "Inter Tight", sans-serif;
    user-select: none;
    transition: .3s;
    letter-spacing: .13rem;
}



button {
    background: transparent;
    border: none;
    color: var(--secondary);
    font-size: medium;
    transition: transform 0.3s;
    cursor: pointer;
}

button:hover {
    color: var(--secondary);
}

input {
    width: 100%;
    background: none;
    outline: none;
    color: var(--secondary);
    border: 2px solid var(--tranparent-20);
    border-radius: .5rem;
    padding: .8rem 1rem;
    font-size: medium;
    letter-spacing: .1rem;
}

.input_holder p {
    font-size: small;
    margin-bottom: .2rem;
    opacity: .7;
}


a {
    text-decoration: none;
    color: var(--tranparent-cc);
}

#dmain {
    padding: 0 0rem;
    color: var(--secondary);
}

.reacticon_1 {
    font-size: larger;
}

#dheader {
    border: 1.5px solid var(--tranparent-20);
    border-radius: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 6rem;
    margin: 1rem;

    position: sticky;
    top: 1rem;
    backdrop-filter: blur(30px);
    z-index: 10;
    overflow: hidden;
    animation: slideAndFadeInFromUp 0.5s ease forwards;
}

#dheader::before {
    content: '';
    position: absolute;
    top: var(--y);
    left: var(--x);
    transform: translate(-50%, -50%);
    background: radial-gradient(#ffb73c35, transparent, transparent);
    width: 16rem;
    height: 16rem;
    transition: .3s, top 0s, left 0s;
    opacity: 0;
    z-index: -1;
}

#dheader:hover::before {
    opacity: 1;
}

#dheader::after {
    content: '';
    inset: 2px;
}

#dheader>div:nth-child(2)>div:nth-child(1)>div:nth-child(1)>button:nth-child(-n+3) {
    display: block;
}

#dheader>div:nth-child(2)>div:nth-child(1)>div:nth-child(1)>button:nth-child(4) {
    display: none;
}

#dlogo_element {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .5rem;
    font-size: 1.3rem;
    cursor: pointer;
}

#dheader>#dlogo_element {
    margin: 0 3rem;
}

#dheader>div:nth-child(2) {
    flex: auto;
    border-left: 1.5px solid var(--tranparent-20);
    height: 100%;
}

#dheader>div>div:nth-child(1) {
    border-bottom: 1.5px solid var(--tranparent-20);
    height: 50%;
    display: flex;
    justify-content: space-evenly;
}

#dheader>div>div:nth-child(1)>div:nth-child(1) {
    flex: 1;
    border-right: 1.5px solid var(--tranparent-20);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

#dheader>div>div:nth-child(1)>div:nth-child(2) {
    flex: .1;
    display: flex;
    align-items: center;
    justify-content: space-evenly;

}

#dheader>div>div:nth-child(1)>div:nth-child(2)>button {
    display: flex;
    align-items: center;
    margin: 0 .5rem;
}

#dheader>div>div:nth-child(1)>div:nth-child(2)>button:nth-child(1) {
    font-size: small;
    background: var(--accent);
    color: white;
    padding: .6rem 1rem;
    border-radius: .7rem;
    letter-spacing: 0;
}

#dheader>div>div:nth-child(1)>div:nth-child(2)>button:nth-child(2) {
    font-size: small;
    height: 2.2rem;
    border: 1.5px solid #ffc30d;
    background: #ffc30d30;
    color: white;
    padding: .6rem 1rem;
    border-radius: .7rem;
    letter-spacing: 0;
}

#dheader>div>div:nth-child(2) {
    height: 50%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

#dheader>div>div>button {
    flex: auto;
    height: 100%;
    font-size: 0.9rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .2rem;
}

.line {
    width: 1.5px;
    height: 100%;
    background: var(--tranparent-20);
}

#dlogo {
    filter: invert(var(--invert-value));
    -webkit-filter: invert(var(--invert-value));
    width: 2.5rem;
    height: 2.5rem;
    animation: rotate 10s linear infinite;
    mix-blend-mode: darken;
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
        /* Start rotation from 0 degrees */
    }

    to {
        transform: rotate(360deg);
        /* Rotate to 360 degrees */
    }
}

.btn_alpha {
    color: var(--transparent-50);
    font-size: 1rem;
    transition: .3s;
    letter-spacing: .13rem;
    display: flex;
    align-items: center;
}

.btn_alpha img {
    width: 1.5rem;
    height: 1.5rem;
    transition: .3s ease-in-out;
}

@keyframes move {
    0% {
        transform: translateY(0) scale(1);
    }

    50% {
        transform: translateY(-10px) scale(1.1) translateX(-80px);
    }

    100% {
        transform: translateY(0) scale(1);
    }
}


@keyframes typing {
    from {
        width: 0
    }

    to {
        width: 60%;
    }
}

@keyframes blink-caret {

    from,
    to {
        border-color: transparent
    }

    50% {
        border-color: var(--accent);
    }
}

.topo_move {
    animation: move 5s infinite;
}

.typing {
    overflow: hidden;
    width: fit-content;
    border-right: .1em solid orange;
    white-space: nowrap;
    animation:
        typing .5s steps(8, end),
        blink-caret .75s step-end infinite;
}

#dhero {
    margin-top: 6rem;
    padding: 6rem 4rem;
    font-size: 4rem;
    font-weight: 500;
    display: flex;
    position: relative;
    overflow: hidden;
}

#dhero_arrow {
    width: 12rem;
    filter: invert(var(--invert-value));
    -webkit-filter: invert(var(--invert-value));
    position: absolute;
    transform: rotate(50deg);
    left: 35vw;
    top: 32rem;
}

#dhero>div:nth-child(1) {
    display: flex;
    flex-direction: column;
    align-items: start;
}

#dhero>div:nth-child(2) {
    flex: 1;
    display: flex;
    align-items: center;

}

#dhero>div:nth-child(1)>div {
    display: flex;
    gap: 2rem;
    margin-top: 1rem;
}

.btn_beta {
    background: var(--secondary);
    padding: .5rem 2rem;
    border-radius: 2rem 1rem;
    color: var(--primary);
    font-size: 1.4rem;
    transition: 0.3s;
    font-weight: 500;

    &:hover {
        color: var(--primary);
        box-shadow: 2px 10px 24px var(--tranparent-50);
    }
}

.btn_theta {
    background: var(--tranparent-10);
    padding: 1rem 4rem;
    border-radius: 2rem 1rem;
    border: 2px solid var(--secondary);
    color: var(--secondary);
    font-size: 1.4rem;
    transition: 0.3s;
    font-weight: 500;

    &:hover {
        box-shadow: 2px 10px 24px var(--tranparent-10);
        background: var(--primary);
    }
}


