#dsection1_heading {
    align-self: center;
    font-size: x-large;
    font-weight: 300;
    margin-top: 12rem;
    display: flex;
    justify-content: center;
}

#dsection1 {
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
    flex-wrap: wrap;
    display: flex;
    justify-content: space-between;
    margin: 2rem 8rem;
    /* margin-top: 12rem; */
}

#dsection1>div {
    flex: auto;
    border: 1px solid var(--tranparent-20);
    backdrop-filter: blur(4px);
    border-radius: 1rem;
    padding: 2rem;
    transition: 1s;
    display: flex;
    flex-direction: column;
    justify-content: center;
    animation: slideAndFadeIn 0.5s ease forwards;

}

#dsection1>div:nth-child(1) {
    flex: .5;
    background: linear-gradient(to right, #2e7bff10, #b876ff05, #ff72fd05, #ff7cac05, #ff7c7c10);
}

#dsection1>div:nth-child(2) {
    flex: .5;
    background: linear-gradient(to right, #2e7bff10, #b876ff05, #ff72fd05, #ff7cac05, #ff7c7c10);
}

#dsection1>div:nth-child(3) {
    transition: .3s;
    flex: .5;
    background: linear-gradient(to right, #2e7bff40, #b876ff20, #ff72fd20, #ff7cac20, #ff7c7c40);

    &:hover {
        flex: .5;
        ;
        background: linear-gradient(to right, #2e7bff40, #b876ff20, #ff72fd20, #ff7cac20, #ff7c7c40);
    }
}


#dsection1>div:hover {
    transform: scale(1.005);
    border: 1px solid var(--tranparent-50);
    box-shadow: 2px 2px 2px 2px #cccccc05;
    background: linear-gradient(to right, #2e7bff10, #b876ff05, #ff72fd05, #ff7cac05, #ff7c7c10);
}

#dsection1>div:hover>button {
    /* transform: scale(1.1); */
    padding: .7rem 4rem;
}


.dsection1_title {
    font-weight: 200;
    font-size: x-large;
}

.dsection1_price {
    font-size: 6rem;
    margin: 0rem 0;
    font-family: "Madimi one", sans-serif;
    align-self: center;
}

#dsection1>div>h3 {
    width: 100%;
    text-align: center;
    font-size: x-large;
}

#dsection1>div>ul {
    margin: 0rem 0;
    align-self: center;
    list-style: none;
    opacity: .8;

}

#dsection1>div>ul>li {
    display: flex;
    align-items: center;
    margin: 1rem 0;
}

#dsection1>div>ul>li::before {
    content: "";
    /* necessary for pseudo-element to appear */
    display: inline-block;
    width: 20px;
    /* desired width */
    height: 20px;
    /* desired height */
    background-image: url('https://static.vecteezy.com/system/resources/previews/010/366/304/original/tick-icon-transparent-free-png.png');
    background-size: contain;
    /* or cover, depending on your preference */
    margin-right: 10px;
    /* adjust spacing */
    filter: invert(var(--invert-value));
}


#dsection1>div>button {
    width: fit-content;
    align-self: center;
    font-size: medium;
    margin-top: 2rem;
    padding: .7rem 2rem;
    border-width: 1px;
}

