#dfaq {
    padding: 2rem 8rem;

}

.dfaq_element {
    background: transparent;
    border: 1.5px solid var(--tranparent-10);
    backdrop-filter: blur(10px);
    padding: 0rem;
    border-radius: 1rem;
    margin-bottom: 1rem;
    letter-spacing: .07rem;
}

.dfaq_element>div:nth-child(1) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    cursor: pointer;
}

.dfaq_element>div:nth-child(1)>p {
    font-size: 1rem;
    opacity: .8;
}


.dfaq_element>div:nth-child(2) {
    transition: .3s;
    padding: 0 2rem 2rem 2rem;
    margin-bottom: 0rem;
    font-size: 1rem;
    opacity: .6;
}



