#ddialog {
    width: 100%;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    background-color: var(--tranparent-50);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 50;
    transition: opacity 0.3s ease-in-out;
}

.ddialog_element {
    background-color: var(--primary);
    color: var(--secondary);
    min-width: 40%;
    width: auto;
    height: fit-content;
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 4px 4px 24px 4px var(--tranparent-20);

    /* Animation */
    animation: slideAndFadeIn 0.5s ease forwards;
    opacity: 0;
    transform: translateY(-50px);
}

@keyframes slideAndFadeIn {
    0% {
        opacity: 0;
        transform: translateY(50px);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}


@keyframes slideAndFadeInFromUp {
    0% {
        opacity: 0;
        transform: translateY(-50px);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}



.ddialog_element>p {
    font-size: large;
    text-align: center;
    padding: 1rem;
}

.ddialog_element>form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.ddialog_element>div>button {
    font-size: medium;
    width: fit-content;
    align-self: center;
    padding: 1rem;
}

.ddialog_close {
    font-size: 2rem;
    cursor: pointer;
    opacity: .5;

    &:hover {
        opacity: 1;
    }
}

/***********/
footer {
    width: 100%;
    padding: 4rem 8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10rem;
    border-top: 2px solid var(--tranparent-20);
    backdrop-filter: blur(40px);
    user-select: text;
}

.footer_element {
    flex: 1;
}

.footer_element:nth-child(1) {
    display: flex;
    align-items: center;
    font-size: .9rem;
    gap: .5rem;
}

.footer_element:nth-child(1)>div {
    display: flex;
    flex-direction: column;
    gap: .2rem;
}

.footer_element:nth-child(1)>div img {
    width: 2rem;
    filter: invert(var(--invert-value));
}

.footer_element:nth-child(2) {
    display: flex;
    justify-content: center;
    cursor: pointer;
}

.footer_element:nth-child(3) {
    text-align: end;
}

footer>div:nth-child(2)>div {
    display: flex;
    align-items: center;
    gap: .5rem;
}

footer>div:nth-child(3)>p:nth-child(1) {
    color: var(--tranparent-cc);
    font-size: small;
    letter-spacing: .08rem;
}

#dhelp_btn {
    display: flex;
    align-items: center;
    position: fixed;
    aspect-ratio: 1/1;
    color: var(--primary);
    background: var(--accent);
    box-shadow: 4px 4px 4px 4px var(--tranparent-20);
    right: 0;
    bottom: 0;
    margin: 1rem;
    border-radius: 100%;
    padding: 1rem;
    cursor: pointer;
    transition: .3s;

    &:hover {
        transform: scale(1.05);
    }
}

.btn_omega {
    height: 3.2rem;
    width: 12rem;
    align-self: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    border: 2px solid var(--secondary);
    border-radius: 2rem 1rem;
    cursor: pointer;
    transition: .3s;
    position: relative;
    overflow: hidden;

}

.btn_omega:hover {
    background: var(--tranparent-20);
}

.btn_omega button {
    width: 100%;
    height: 100%;
}

.btn_omega>div {
    position: absolute;
    left: 40%;
    bottom: -.2rem;
    overflow: hidden;
    height: 100%;
}



.circular-loading-spinner {
    border: 4px solid rgba(0, 0, 0, 0.02);
    border-left-color: var(--secondary);
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 1s infinite linear;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}


#dalert {
    position: fixed;
    left: 0;
    top: 10rem;
    width: 100%;
    z-index: 10;
    display: flex;
    justify-content: center;
}

#dalert_element {
    background: var(--primary);
    min-width: 20rem;
    box-shadow: 8px 8px 24px 4px var(--tranparent-10);
    border-radius: .5rem;
    padding: 1rem;
    margin-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    gap: .5rem;
    transition: .3s;
    position: fixed;
}

#dalert_element_icon {
    font-size: x-large;
    display: flex;
    align-items: center;
}




/******/
.derror {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 4rem;
}

.derror>p:nth-child(1) {
    font-size: 2rem;
    font-weight: 400;
}

.derror>footer {
    position: absolute;
    bottom: 0;
    left: 0;
}





