#dcollab_body {
    display: flex;
    width: 100%;
    padding: 0 1rem;
    gap: 1rem;
}

#dcollab_body>div:nth-child(1) {
    width: 20%;
    height: fit-content;
    padding: 1rem;
    border-radius: 1rem;
    border: 1.5px solid var(--tranparent-20);
    display: flex;
    flex-direction: column;
    position: sticky;
    top: 5.7rem;
    gap: .5rem;
}

#dcollab_body>div:nth-child(2) {
    width: 80%;
    height: 200vh;
    padding: 1rem;
    border-radius: 1rem;

    border: 0px solid var(--tranparent-20);
    display: flex;
    flex-direction: column;
    gap: .5rem;
}


.dsidemenu_element{
    padding: .5rem;
    opacity: 1;
    display: flex;
    align-items: center;
    gap: .6rem;
    font-size: .9rem;
    transition: .3s;
    cursor: pointer;
    border-radius: .5rem;

    &:hover{
        opacity: 1;
        background: var(--tranparent-20);
    }
}

.dsidemenu_element>svg{
    font-size: 1.4rem;

}