@media only screen and (max-width: 860px) {

    /* .btn_alpha{
        font-size: .8rem;
    } */


    .btn_alpha{
        font-size: small;
    }


    #dheader {
        height: 6rem;
        width: auto;
        margin: 0 .5rem;
        position: relative;
    }

    #dlogo_element {
        margin: 0 .5rem;
    
    }

    #dheader>div>div>button {
        font-size: small;
    }


    #dheader>div:nth-child(1) {
        gap: .2rem;
        font-size: medium;
        padding: 1rem;
    }

    #dheader>div>div:nth-child(1)>div:nth-child(2) {
        padding: 1rem;
    }

    #dheader>div>div:nth-child(1)>div:nth-child(1) {
        gap: .2rem;
    }

    /** navs **/
    #dheader>div:nth-child(2)>div:nth-child(1)>div:nth-child(1)>button:nth-child(-n+3) {
        display: none;
    }

    #dheader>div:nth-child(2)>div:nth-child(1)>div:nth-child(1)>button:nth-child(4) {
        display: block;
    }


    #bg_glow1 {
        width: 30rem;
        height: 20rem;
        left: -30rem;
        top: 5rem;
    }



    /****/


    #dhero {
        width: auto;
        overflow: hidden;
    }

    #dhero>div:nth-child(2) img {
        height: 12rem;
        position: absolute;
        right: 0;
        z-index: -1;
    }

    #dlogo {
        width: 1.6rem;
        height: 1.6rem;
    }

    #dhero {
        margin-top: 4rem;
        padding: 2rem;
        font-size: 2rem;
    }

    #dhero_arrow {
        width: 8rem;
        transform: rotate(50deg);
        left: 70vw;
        top: 22rem;
    }

    #dhero>div>div {
        gap: 0rem;
    }

    #dhero>div>div>button {
        padding: .8rem 1.6rem;
        font-size: 1.2rem;
    }

    .rotating-item {
        display: none;
    }


    #bg_glow1 {
        right: 0%;
        transform: rotate(90deg);
    }




    /***********/

    #dsection1 {
        margin: 2rem 2rem;
        display: flex;
        flex-direction: column;
    }

    #dsection1_heading{
        font-size: medium;
    }


    #dsection1>div {
        width: 100%;
    }

    .dsection1_title{
        font-size: large;
    }
    
    .dsection1_price{
        font-size: 4rem;
    }
    
    #dsection1 > div > ul {
        font-size: small;
    }

    #dsection1 > div > ul > li {
        margin: .5rem 0;
    }
    

    



    /*****/

    #dfaq {
        padding: 2rem;
    }

    .dfaq_element {
        margin-bottom: 1rem;
    }

    .dfaq_element>div:nth-child(1) {
        padding: 1rem;
    }


    .dfaq_element>div:nth-child(2) {
        padding: 1rem;
    }

    footer {
        padding: 2rem;
        font-size: .8rem;
    }

    .footer_element:nth-child(1)>div img {
        width: 1.2rem;
    }

    .footer_element:nth-child(1)>div {
        font-size: .6rem;
    }



    .ddialog_element {
        width: 90%;
    }





    /*******/
    #dcollab_body>div:nth-child(1) {
        display: none;
    }
}